import React, { Component } from "react";
import "./Kantoor.css";
import "../general.css";

class Kantoor extends Component {
	render() {
		return(
			<div className="body">
				<div className="subjectHeader">
					Kantoor
				</div>
				<div className="kantoorContent">
					<p className="intro">
						Dorhout advocatuur is de eenmanszaak van de advocaat mr.G.R.Dorhout - Tielken. Aan het kantoor is haar echtgenoot mr.A.F.Dorhout verbonden als juridisch adviseur.
					</p>
					<div className="participants">
					
						<div className="participant">
							<div className="name">
								Mr.G.R.Dorhout - Tielken, advocaat
							</div>
							<div className="partText">
								<p>
								Germaine Dorhout heeft rechten gestudeerd aan de Rijksuniversiteit Groningen en is sinds 1 januari 1989 advocaat in Soest.Het accent van haar algemene praktijk ligt op het personen - en familierecht, waaronder in het bijzonder het jeugdrecht.Zij heeft veel ervaring in echtscheidingsprocedures en een breed scala aan overige familie - en jeugdrechtzaken.Zij wordt met enige regelmaat door de Rechtbank Midden - Nederland benoemd tot bijzonder curator.De werkwijze van Germaine Dorhout kenmerkt zich door een directe aanpak en een persoonlijke benadering van cliënten.Waar mogelijk zal zij steeds streven naar het voorkomen van(verdere) escalaties en het in samenwerking met de cliënt bereiken van het meest optimale resultaat.
								</p>

								<p>
								Naast haar advocatenpraktijk is Germaine Dorhout sinds 1 januari 2005 voorzitter van de klachtencommissie van een landelijk werkende jeugdzorginstelling(GI).
								</p>
							</div>
						</div>
						<div className="participant">
							<div className="name">
								Mr.A.F.Dorhout, juridisch adviseur
							</div>
							<div className="partText">
								<p>
								Toon Dorhout heeft rechten gestudeerd aan de Universiteit Leiden.Na zijn studie heeft hij 11 jaar gewerkt bij een grote internationaal werkende onderneming in Utrecht.In 1981 is Toon Dorhout als advocaat beëdigd in Amsterdam.Vanaf 1989 tot 2002 was hij als advocaat werkzaam in Soest.Hij hield zich, naast de algemene praktijk, voornamelijk bezig met het adviseren van bedrijven op het gebied van ondernemings - , vennootschaps - , arbeids - en bouwrecht.Om fysieke redenen heeft Toon Dorhout zich in 2002 laten uitschrijven als advocaat.Daarna is hij als juridisch adviseur aan het kantoor verbonden gebleven.Als advocaat en vervolgens als juridisch adviseur hecht Toon Dorhout veel waarde aan een grondige analyse van de achtergronden en oorzaken van problemen en geschillen.Dit leidt tot beter begrip van de zaak en draagt op die wijze bij aan het bereiken van een oplossing.
								</p>
							</div>
						</div>
					</div>
				</div>
			</div>
		)
	}
}

export default Kantoor;