import React, { Component } from "react";
import "./header.css";

class Header extends Component {

	render(){
		return(
			<div className="headerBody">
				<div className="logo">
					Dorhout Advocatuur
				</div>
				<div className="menuItems">
					<ul className="items">
						<li className="item">Kantoor</li>
						<li className="item">Rechtsgebieden</li>
						<li className="item">Kosten</li>
						<li className="item">Algemene voorwaarden</li>
						<li className="item">Contact</li>
					</ul>
				</div>
			</div>
		)
	}
}

export default Header;